import {useState} from 'react';
import { useNavigate } from "react-router-dom";
import styles from './Main.module.css';
import Header from '../../Header/Header';
import Map from "../../Map/Map" 
import Slide from '../../Slide/Slide';
import Text from "../../Text/Text";
import MainText from "../../../Content/MainText";
import Button from '../../Button/Button';
import Modal from '../../Modal/Modal';
/*
<Text style={{height: "100%", fontSize: "15px"}}><MainText/></Text>
*/
const Main = () => {
  let navigate = useNavigate();
  const [ModalVideo, setModalVideo] = useState(false)
  return (
    <>
    <Modal isOpen={ModalVideo} onModalClose={() => setModalVideo(false)} ColorClose={"#fff"}>
      <video style={{width: "100%"}} src="/Content/Video.mp4" controls="controls"></video>
    </Modal>
    <Slide>
      <Header/>
      <div className={styles.ContainerMapText}>
        <div style={{flex: 3.5}}>
          <Map/>
        </div>
        <div style={{flex: 6.5}}>
          <div className={styles.ContainerTextImage}>
            <div className={styles.Text}>
              <p style={{textAlign: "center"}}>Уважаемые посетители виртуального музея!</p>
              <p>История развития прокуратуры России как государственного органа, осуществляющего надзор за исполнением законов, имеет трёхвековую  давность. В 1722 году, когда Пётр I учредил в государстве Российском абсолютно новый для нашего Отечества орган, страна входила в эпоху преобразований, и без создания специального надзорного института невозможно было укрепить державные устои и обеспечить режим законности. </p>
              <p>С тех пор прошло много времени, но все эти годы прокуратура выполняла своё историческое предназначение – верой и правдой служить России, стоять на страже закона.</p>
              <p>Прокуратура Пермского края по праву гордится своими достижениями и высокопрофессиональными сотрудниками, которые с честью несут свою нелегкую службу в рядах прокуратуры Российской Федерации.</p>
              <p>Это электронное издание о людях, посвятивших свою жизнь служению Закону - работниках органов прокуратуры края.</p>
              <p style={{textAlign: "right", fontWeight: "normal"}}>Павел Бухтояров прокурор края</p>
            </div>
            <img className={styles.Image} src="Content/MainSlide/Prosecutor.JPG" alt=""/>
          </div>
          <Text style={{height: 125, margin: "20px 0px", fontSize: "1vw"}}><MainText/></Text>
          <img className={styles.ImageVideo} src={"/Content/poster.png"} onClick={() => setModalVideo(true)}/>
        </div>
      </div>
      <div className={styles.ButtonsPanel}>
        <Button active={() => navigate("/history")}  Text={"История органов прокуратуры России"}/>
        <Button active={() => navigate("/prosecutors")} Text={"Становление прокуратуры Прикамья"}/>
        <Button active={() => navigate("/prosecutors_in_faces")} Text={"Прокуратура Пермского края в лицах"}/>
        <Button active={() => navigate("/publications")} Text={"Печатные издания прокуратуры края"}/>
      </div>
    </Slide>
    </>
  )
}

export default Main