import styles from './Text.module.css';

const Text = (props) => {
  return (
    <div className={styles.Container} style={props.style}>
      <div className={styles.Text} style={{maxHeight: (!!props.OpenText)? "calc(100% - 35px)": "100%"}}>
        {props.children}
      </div>
      {
      !!props.OpenText && <div className={styles.OpenButton} onClick={props.OpenText}>
        Показать все &#709;
      </div>
      }
    </div>
  )
}

export default Text