import { useNavigate } from "react-router-dom";
import styles from './Publications.module.css';
import Header from '../../Header/Header';
import Slide from '../../Slide/Slide';
import Button from '../../Button/Button';
import Title from "../../Title/Title"

const Data = [
  {
    Image: "Content/Publications/BookCover.png", 
    Name: "«Название книги» Автор А.А 1950 г.",
    Link: "Content/Publications/BookCover.png"
  },
  {
    Image: "Content/Publications/BookCover.png", 
    Name: "«Название книги» Автор А.А 1950 г.",
    Link: "Content/Publications/BookCover.png"
  },
  {
    Image: "Content/Publications/BookCover.png", 
    Name: "«Название книги» Автор А.А 1950 г.",
    Link: "Content/Publications/BookCover.png"
  },
  {
    Image: "Content/Publications/BookCover.png", 
    Name: "«Название книги» Автор А.А 1950 г.",
    Link: "Content/Publications/BookCover.png"
  },
  {
    Image: "Content/Publications/BookCover.png", 
    Name: "«Название книги» Автор А.А 1950 г.",
    Link: "Content/Publications/BookCover.png"
  },
  {
    Image: "Content/Publications/BookCover.png", 
    Name: "«Название книги» Автор А.А 1950 г.",
    Link: "Content/Publications/BookCover.png"
  },
]

const Publications = (props) => {
  let navigate = useNavigate();
  
  return (
    <>
    <Slide>
      <Header/>
      <Title Text={"Печатные издания прокуратуры края"}/>
      <div className={styles.ListBook}>
        {
          Data.map(el => {
            return (
              <div className={styles.CardBook}>
                <img className={styles.CardImage} src={el.Image} alt="Book" />
                <a href={el.Link}><div className={styles.CardText}>{el.Name}</div></a>
              </div>
            )
          })
        }
      </div>
      <div className={styles.BackButton}>
        <Button Text={"Назад"} active={() => navigate("/")}/>
      </div>
    </Slide>
    </>
  )
}

export default Publications